// link mixins
@mixin link-reset(){
  border: none;
  background: none;
  font-family: inherit;
  font-size: inherit;
  text-decoration: none;
  display: block;
  line-height: 150%;
}
@mixin link-look(){
  @include link-reset();
  padding: 17px 2vw;
}

// message mixin
@mixin message($color){
  background: lighten($color,35%);
  box-shadow:inset 0 0 2px darken($color,15%);
  color: darken($color,15%);
  padding: 7px 10px;
  p{
	padding: 0;
  }
}

// delete button mixin
@mixin button-look($color){
  border: 1px solid darken($color,15%);
  background: lighten($color,35%);
  color: darken($color,15%);
  border-radius: 3px;
  padding: 3px 8px;
  &:hover{
	background: darken($color,15%);
	color: white;
  }
  &:active{
	background: lighten($color,35%);
  }
  &:disabled{
	background: $nut-color;
	color: gray;
	border-color: gainsboro;
  }
}

// Typography
$base-font: "Helvetica Neue", Helvetica, Arial, sans-serif;

// colors
$prim-color: #ff8505;
$sec-color: lighten($prim-color, 30%);
$nut-color: whitesmoke;

