@charset "UTF-8";
/* Variables */
/*$brand-success: #2ab27b;
$brand-warning: #cbb956;
$brand-danger: #bf5329;*/
*, html {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 1em;
}

header {
  background: whitesmoke;
  display: flex;
  border-bottom: 1px solid gainsboro;
  align-items: baseline;
  padding-right: 20px;
}

header h1 a {
  border: none;
  background: none;
  font-family: inherit;
  font-size: inherit;
  text-decoration: none;
  display: block;
  line-height: 150%;
  font-weight: 200;
  font-size: .8em;
  padding: 15px 20px;
  color: #eb7800;
}

header h1 a:hover {
  color: #ff8505;
}

nav {
  display: flex;
  justify-content: space-between;
  flex: 1;
}

nav ul {
  list-style: none;
  display: flex;
}

nav ul .active a {
  color: black;
  border-bottom: 2px solid #ff8505;
}

nav #login {
  display: flex;
  margin-left: auto;
}

nav #login #user {
  border: none;
  background: none;
  font-family: inherit;
  font-size: inherit;
  text-decoration: none;
  display: block;
  line-height: 150%;
  padding: 17px 2vw;
  color: black;
}

nav ul li a, nav #login li input {
  border: none;
  background: none;
  font-family: inherit;
  font-size: inherit;
  text-decoration: none;
  display: block;
  line-height: 150%;
  padding: 17px 2vw;
  cursor: pointer;
  color: #666666;
}

nav ul li a:hover, nav #login li input:hover {
  color: black;
}

/* main content */
main {
  padding: 20px;
}

main a.back {
  display: block;
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  color: #ff8505;
  text-decoration: none;
  margin-bottom: 15px;
  padding-left: 10px;
  transition: all 5ms ease;
}

main a.back:hover {
  padding-left: 0;
}

main a.back::before {
  content: '⬅︎ ';
}

main section {
  max-width: 1000px;
  width: 100%;
  margin: 0 auto;
  border: 1px solid gainsboro;
  border-radius: 3px;
  position: relative;
}

main section h2 {
  padding: 6px 12px;
  font-weight: normal;
  font-size: 1.1em;
  background: linear-gradient(#fafafa, whitesmoke);
  border-radius: 3px 3px 0 0;
  color: #0d0d0d;
}

main section a.button {
  border: 1px solid #b30000;
  background: #ffb3b3;
  color: #b30000;
  border-radius: 3px;
  padding: 3px 8px;
  margin: 4px;
  font-size: .8em;
  text-decoration: none;
  position: absolute;
  right: 0;
  top: 0;
}

main section a.button:hover {
  background: #b30000;
  color: white;
}

main section a.button:active {
  background: #ffb3b3;
}

main section a.button:disabled {
  background: whitesmoke;
  color: gray;
  border-color: gainsboro;
}

main section .error {
  background: #ffb3b3;
  box-shadow: inset 0 0 2px #b30000;
  color: #b30000;
  padding: 7px 10px;
}

main section .error p {
  padding: 0;
}

main section .error ul {
  padding-left: 20px;
}

main section .success {
  background: #c2f0c2;
  box-shadow: inset 0 0 2px #239023;
  color: #239023;
  padding: 7px 10px;
}

main section .success p {
  padding: 0;
}

main section .confirm {
  background: #ffe4b3;
  box-shadow: inset 0 0 2px #b37400;
  color: #b37400;
  padding: 7px 10px;
}

main section .confirm p {
  padding: 0;
}

main section .confirm form {
  padding: 0;
}

main section .confirm form input[type=submit] {
  border: 1px solid #b30000;
  background: #ffb3b3;
  color: #b30000;
  border-radius: 3px;
  padding: 3px 8px;
  float: left;
  margin: 10px 0 7px;
}

main section .confirm form input[type=submit]:hover {
  background: #b30000;
  color: white;
}

main section .confirm form input[type=submit]:active {
  background: #ffb3b3;
}

main section .confirm form input[type=submit]:disabled {
  background: whitesmoke;
  color: gray;
  border-color: gainsboro;
}

main section .confirm a {
  border: 1px solid #5a5a5a;
  background: #d9d9d9;
  color: #5a5a5a;
  border-radius: 3px;
  padding: 3px 8px;
  float: left;
  margin: 10px 10px 7px;
  text-decoration: none;
}

main section .confirm a:hover {
  background: #5a5a5a;
  color: white;
}

main section .confirm a:active {
  background: #d9d9d9;
}

main section .confirm a:disabled {
  background: whitesmoke;
  color: gray;
  border-color: gainsboro;
}

main section .confirm::after {
  content: "";
  display: block;
  clear: both;
}

main section form {
  line-height: 150%;
}

main section form label.login {
  float: left;
}

main section form a.login {
  padding: 0 25px;
}

main section form input {
  padding: 5px 10px;
  font-size: inherit;
  width: calc(100% - (2 * 12px));
  margin-bottom: 15px;
}

main section form input.has-error {
  border: 1px solid #e60000;
  outline-color: #e60000;
}

main section form input[type=submit] {
  border: 1px solid #b85e00;
  background: #ffdcb8;
  color: #b85e00;
  border-radius: 3px;
  padding: 3px 8px;
  margin: 20px 0 10px;
  width: initial;
}

main section form input[type=submit]:hover {
  background: #b85e00;
  color: white;
}

main section form input[type=submit]:active {
  background: #ffdcb8;
}

main section form input[type=submit]:disabled {
  background: whitesmoke;
  color: gray;
  border-color: gainsboro;
}

main section form input[type=submit]:disabled {
  background: whitesmoke;
}

main section form input[type=submit]:disabled:hover {
  color: gray;
}

main section form input[type=checkbox] {
  width: initial;
  float: left;
  margin: 6px 8px 6px 0;
}

main section p, main section form {
  padding: 15px 20px;
}

main section p, main section label, main section input {
  display: block;
}

main section article {
  display: block;
}
